ve-color, $right-active-color);\n        }\n\n        &[disabled],\n        &.btn-disabled {\n          background-color: nb-theme(button-hero-#{$status}-disabled-background-color);\n          background-image: none;\n          color: nb-theme(button-hero-#{$status}-disabled-text-color);\n        }\n\n        &.button-pulse {\n          animation: button-hero-#{$status}-pulse 0.75s infinite alternate;\n        }\n\n        @include keyframes(button-hero-#{$status}-pulse) {\n          0% {\n            box-shadow: none;\n            opacity: 0.3;\n          }\n\n          100% {\n            box-shadow: 0 0 1rem 0 $left-color;\n            opacity: 0.8;\n          }\n        }\n      }\n    }\n  }\n}\n","/**\n * @license\n * Copyright Akveo. All Rights Reserved.\n * Licensed under the MIT License. See License.txt in the project root for license information.\n */\n\n@import './input-statuses';\n@import './input-sizes';\n@import './input-shapes';\n\n@mixin nb-input-theme() {\n\n  [nbInput] {\n    border-style: nb-theme(input-border-style);\n    border-width: nb-theme(input-border-width);\n    font-family: nb-theme(input-text-font-family);\n    -webkit-appearance: none; // removes inner shadow on iOS\n\n    &.nb-transition {\n      @include nb-component-animation(border, background-color, color, box-shadow);\n    }\n\n    &::placeholder {\n      font-family: nb-theme(input-placeholder-text-font-family);\n      text-overflow: ellipsis;\n    }\n\n    &:focus {\n      outline: none;\n    }\n\n    &.input-full-width {\n      width: 100%;\n    }\n\n    @include input-statuses();\n    @include input-sizes();\n    @include input-shapes();\n  }\n\n  @each $size in nb-get-sizes() {\n    @include nb-form-field-with-prefix('[nbInput].size-#{$size}', $size);\n    @include nb-form-field-with-suffix('[nbInput].size-#{$size}', $size);\n  }\n}\n","/**\n * @license\n * Copyright Akveo. All Rights Reserved.\n * Licensed under the MIT License. See License.txt in the project root for licen